.productsBox {
  position: relative;
  margin-top: 70px;
  overflow: hidden;
}

.productsTitle {
  margin-left: var(--mainPadding);
  margin-bottom: 5vw;
  font-size: 2.5vw;
}

.products {
  min-width: 100%;
  display: flex;
  position: relative;
  margin-left: var(--mainPadding);
}

.productsBtn {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  padding: 0 7px;
  top: 50%;
  left: 90%;
  background-color: white;
  border: none;
  font-size: 2.2rem;
  cursor: pointer;
}

.productsBtnLeft {
  left: 0;
  margin-left: var(--mainPadding);
}
